
// Vue
import { computed, defineComponent } from 'vue'
import { useRoute } from 'vue-router'
// Components
import svgWrapper from '@/components/elements/svg/index.vue'

export default defineComponent({
  name: 'Link',
  components: {
    svgWrapper
  },
  props: {
    /**
     * Link route
     */
    route: {
      type: String,
      required: true
    },
    /**
     * Link text
     */
    text: {
      type: String,
      required: false,
      default: 'Link'
    },
    /**
     * Link icon
     */
    icon: {
      type: String,
      required: false,
      default: 'dashboard'
    },
    /**
     * Link color
     */
    color: {
      type: String,
      required: false,
      default: 'mint'
    }
  },
  setup () {
    /**
     * Context
     */
    const route = useRoute()

    /**
     * Data
     */
    // # Route current path
    const routeCurrentPath = computed<any>(() => route.fullPath)

    /**
     * Return
     */
    return {
      routeCurrentPath
    }
  }
})
