
// Vue
import { defineComponent, computed } from 'vue'
import { useStore } from 'vuex'
// Types
import { AppGettersEnum } from '@/types/app/types'
// Components
import Navigation from '@/components/Navigation/index.vue'
import PanelWrapper from '@/components/Navigation/includes/PanelWrapper/index.vue'

export default defineComponent({
  name: 'App',
  components: {
    Navigation,
    PanelWrapper
  },
  setup () {
    /**
     * Context
     */
    const store = useStore()

    /**
     * Data
     */
    const viewStatus = computed<boolean>(() => store.getters[AppGettersEnum.GetControlPanelViewStatus])

    /**
     * Return
     */
    return {
      // Data
      viewStatus
    }
  }
})
